#ace_editor{
	position:relative;
	top:0;
	left:0;
	width:100% !important;
	height:100% !important;
}
.split{
	display:flex;
	flex-direction:row;
	position:fixed;top:0;left:0;width:100%;
	height:100%;
}
.splitItem{
	flex: 1;
}
.scroll{
	overflow:scroll;
}

.error {
	background-color:#ffdddd !important;
}
